html, body {
  text-align: center;
  background: 
  -webkit-gradient(
      linear, 
      left top, left bottom, 
      from(rgba(0, 0, 0, 0.25)), 
      to(rgba(0, 0, 0, 0.25))
    ),
  url('/background.jpg') no-repeat center center fixed;
  background: 
  -webkit-linear-gradient(
      rgba(0, 0, 0, 0.25), 
      rgba(0, 0, 0, 0.25)
    ),
  url('/background.jpg') no-repeat center center fixed;
  background: 
  -o-linear-gradient(
      rgba(0, 0, 0, 0.25), 
      rgba(0, 0, 0, 0.25)
    ),
  url('/background.jpg') no-repeat center center fixed;
  background: 
  linear-gradient(
      rgba(0, 0, 0, 0.25), 
      rgba(0, 0, 0, 0.25)
    ),
  url('/background.jpg') no-repeat center center fixed;
  background-size: cover;
}

.Grid {
  margin: 5%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.static-logo {
  height: 150px;
  cursor: pointer;
}

.menu {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  top: 145px;
  left: 0;
  right:0;
  font-weight: 800;
  font-size: 35px;
  text-shadow: 0px 0px 5px rgba(0,0,0,0.42);
  cursor: pointer;
}

.menu-hidden {
  visibility: hidden;
}

.App-header {
  height: 150px;
  padding: 20px;
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



